import { Avatar, Typography } from "@mui/material";
import React, { useContext, useEffect, useState } from "react";
import styled from "styled-components";
import NormalButton from "../../../atomic/NormalButton";
import ToolTips from "../../ToolTips";
import { isBrowser } from "react-device-detect";
import roleImg01 from "../../../../assets/images/roleImg01.png";
import roleImg02 from "../../../../assets/images/roleImg02.png";
import roleImg03 from "../../../../assets/images/roleImg03.png";
import roleImg04 from "../../../../assets/images/roleImg04.png";
import roleImg05 from "../../../../assets/images/roleImgUser.png";
import ErrorIcon from "@mui/icons-material/Error";
import emojiFire from "../../../../assets/images/emo_fire.png";
import { LoginContext } from "../../../../utils/providers/login/LoginContext";
import RecommandBookCard from "../../modal/RecommandBookCard";
import book1 from "../../../../assets/images/book1.jpg";
import book2 from "../../../../assets/images/book2.jpg";
import book3 from "../../../../assets/images/book3.jpg";
import book4 from "../../../../assets/images/book4.jpg";

export default function RoleContent({ handleClickOpen }) {
  const roleList = [
    {
      img: roleImg01,
      name: " ",
      title: "  ",
      desc: "2023 8          1 . ",
      thumnail: book1,
      createAt: "2023 09 07",
    },
    {
      img: roleImg02,
      name: " ",
      title: "    ",
      desc: "      .  , ,  . AI       ,        .",
      thumnail: book2,
      createAt: "2024 01 24",
    },
    {
      img: roleImg03,
      name: " ",
      title: "   ",
      desc: "    10,          ,      ",
      thumnail: book3,
      createAt: "2023 11 24",
    },
    {
      img: roleImg04,
      name: "  ",
      title: " ",
      desc: "           .               .    ,       ,     . ",
      thumnail: book4,
      createAt: "2023 08 30",
    },
  ];
  const UserRole = { img: roleImg05, name: " " };

  const tipTitle = null;
  const tipDesc = (
    <span>
         ?  !
      <br />
        
    </span>
  );

  const { isLogined, setIsLogined } = useContext(LoginContext);

  const [modalOpen, setModalOpen] = useState(false);
  const [modalInfo, setModalInfo] = useState("");
  const [ItemList, setItemList] = useState();
  const [myRecord, setMyRecord] = useState();

  //     
  const handleUsesItemDetail = (role) => {
    setModalOpen((prev) => !prev);
    setModalInfo(role);
  };

  useEffect(() => {
    setItemList(roleList);
  }, []);
  useEffect(() => {
    setMyRecord(UserRole);
  }, [isLogined]);

  return (
    <RoleSection>
      <Title variant="">
        <img src={emojiFire} alt=""></img>   
        <Typography sx={{ margin: "0 0 0 7px" }} variant="" color={"primary"}>
          
        </Typography>
         ?
        {isBrowser ? (
          <ToolTips position={"right"} title={tipTitle} desc={tipDesc}>
            <CusNoticeIcon color="" />
          </ToolTips>
        ) : (
          <NormalButton
            callBackFunc={(e) => {
              handleClickOpen(tipTitle, tipDesc);
            }}
            buttonTitle={<CusNoticeIcon color="" />}
          ></NormalButton>
        )}
      </Title>
      <RoleList>
        {ItemList?.map((role, index) => {
          return (
            <RoleListItem
              onClick={() => {
                handleUsesItemDetail(role);
              }}
              key={index}
            >
              <RoleImg variant="circular" src={role.img} />
              <RoelName variant="subtitle1">{role.name}</RoelName>
            </RoleListItem>
          );
        })}

        {isLogined && (
          <RoleListItem
            onClick={() => {
              setModalInfo(null);
              setModalOpen((prev) => !prev);
            }}
            className="roleUser"
          >
            <RoleImg variant="circular" src={UserRole.img} />
            <RoelName variant="subtitle1">{UserRole.name}</RoelName>
          </RoleListItem>
        )}

        {modalOpen && (
          <RecommandBookCard
            modalOpen={modalOpen}
            setModalOpen={setModalOpen}
            modalInfo={modalInfo}
          />
        )}
      </RoleList>
    </RoleSection>
  );
}

const RoleSection = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 20px 0;
  margin-bottom: 10px;
`;

const RoleList = styled.ul`
  display: flex;
  width: 100%;
  padding: 10px 0;

  overflow-x: auto;

  @media (min-width: 481px) {
  }
`;

const RoleListItem = styled.li`
  display: flex;
  flex-direction: column;
  flex: 0 0 30%;

  align-items: center;
  justify-content: center;
  margin: 0;

  @media (min-width: 481px) {
    flex: 0 0 15%;
  }

  &:hover {
    cursor: pointer;
    opacity: 0.8;
  }

  &.roleUser {
    margin-left: 10%;
    position: relative;
    &:after {
      content: "";
      position: absolute;
      width: 2px;
      height: 100%;
      left: 5px;
      top: 0;
      background: #dcdcdc;
    }

    @media (min-width: 481px) {
      &:after {
        content: "";
        position: absolute;
        width: 2px;
        height: 100%;
        left: -50%;
        top: 0;
        background: #dcdcdc;
      }
    }
  }
`;

const Title = styled(Typography)`
  display: flex;
  align-items: center;
  width: 100%;

  font-size: 17px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: -0.75px;

  margin-bottom: 20px;

  & img {
    margin-right: 5px;
  }
`;

const RoleImg = styled(Avatar)`
  /* border: 1px solid #dcdcdc; */
  width: 60px;
  height: 60px;
  margin-bottom: 10px;
  /* overflow: none; */
  & img {
    width: 55px;
    height: 55px;
    object-fit: contain;
  }
`;

const RoelName = styled(Typography)`
  font-size: 14px;
  font-weight: 400;
  line-height: 17px;
`;
const CusNoticeIcon = styled(ErrorIcon)`
  color: #3cb371;
`;
